header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: #fff;
  min-width: 1024px;
  height: 60px;
  padding: 0 50px;
}
    
nav {
  height: 60px;
  display: flex;
  align-items: center;
  text-transform: uppercase;
}
   
.from-header {
  width: 40px;
  height: 40px;
}
    
a {
  text-decoration: none;
  color: #000;
  font-size: 12px;
  line-height: 14px;
}
    
a:not(:first-child) {
  margin-left: 20px;
}
    
a:hover {
  color: #00acdc;
}
   
.is-active {
  position: relative;
  color: #00acdc;
}
    
.is-active::after {
  content: "";
  left: 0;
  top: 33px;
  width: 100%;
  height: 4px;
  background: #00acdc;
  border-radius: 8px;
  position: absolute;
}
     
.container {
  display: flex;
  flex-flow: column nowrap;
  min-width: 1024px;
  max-width: 1200px;
  height: 1050px;
  margin: auto;
  background: #fff;
}
     
main {
  display: flex;
  flex-flow: wrap;
  align-items: center;
  justify-content: center;
  background: #fff;
  width: 994px;
  height: 990px;
  margin: 25px auto;
}
     
.wrap {
  width: 200px;
  border: 1px solid #f3f3f3;
  box-sizing: border-box;
  border-radius: 5px;
  margin: 25px 24px;
}
  
.img1 {
  width: 158px;
  height: 132px;
  margin: 30px 21px 43px;
}
  
.name {
  width: 168px;
  height: 31px;
  font-weight: 500;
  font-size: 12px;
  line-height: 15px;
  margin: 0 auto 4px;
}
  
.code {
  margin-left: 15px;
  font-size: 11px;
  line-height: 13px;
}
  
.review {
  display: flex;
  justify-content: space-between;
  margin: 15px 15px 0;
  align-items: center;
}
  
.review-text {
  font-size: 11px;
  line-height: 13px;
  margin-left: 15px;
}
  
.star {
  width: 15px;
  height: 15px;
  background: url(images/star-active.svg) no-repeat 1px 2px ;
}
  
.star:nth-child(5) {
  background: url(images/star.svg) no-repeat 1px 2px;
}
  
.price {
  display: flex;
  padding-top: 20px;
  justify-content: space-between;
  margin: 0 15px;
  align-items: flex-end;
}
  
.cost {
  font-size: 11px;
}
  
.volume {
  font-weight: bold;
  font-size: 16px;
  line-height: 19px;
}
  
.wrap-button {
  display: flex;
  width: 168px;
  margin: 20px auto 15px;
}
  
.submit {
  padding-top: 13px;
  width: 168px;
  height: 29px;
  text-align: center;
  text-decoration: none;
  color: #fff;
  background-color: #00acdc;
  font-weight: bold;
  font-size: 16px;
  line-height: 16px;
  text-transform: uppercase;
  border-radius: 5px;
  border: 1px solid  #00acdc;
}
  
.submit:hover {
  color: #00acdc;
  background-color: #fff;
}
